.block {
  margin: 0 auto;
  padding: 70px 0 60px;
  display: flex;
  max-width: 750px;
}

.img {
  margin-right: 80px;
  max-width: 100%;
  height: 355px;
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.25);
}

.img img {
  height: 100%;
}

.descr {
  margin-top: 25px;
  text-align: center;
}

@media (max-width: 992px) {
  .img {
    margin-right: 30px;
  }
}

@media (max-width: 768px) {
  .block {
    padding: 40px 0;
    flex-direction: column;
    align-items: center;
  }

  .img {
    margin-right: 0;
    margin-bottom: 20px;
    height: 250px;
  }

  .img img {
    width: 100%;
    object-fit: cover;
  }

  .descr {
    margin-top: 20px;
  }
}

@media (max-width: 576px) {
  .block {
    padding: 30px 0;
  }

  .img {
    height: 150px;
  }
}