.block {
  padding: 30px 0 85px;
  color: #fff;
}

.title {
  margin-top: 60px;
  text-align: center;
  font-weight: 700;
  font-size: 40px;
  line-height: 58px;
}

@media (max-width:768px) {
  .block {
    padding: 30px 0 50px;
  }

  .title {
    margin-top: 30px;
    font-size: 30px;
    line-height: 45px;
  }

}

@media (max-width:576px) {
  .block {
    padding: 20px 0 40px;
  }

  .title {
    font-size: 22px;
  }

}